/* Title Animation */
        
          
.container{
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
  }
  .container span{
    text-transform: uppercase;
    display: block;
  }
  .text1{
    color: #ffffff;
    font-size: 100px;
    font-weight: 700;
    letter-spacing: 2px;
    word-spacing: 4px;
    margin-bottom: 20px;
    background: #0B0C10;;
    position: relative;
    -webkit-animation: text 2.5s 1;
            animation: text 2.5s 1;
    font-family: 'Lato', sans-serif;
  }
  .text2{
    font-size: 30px;
    font-family: 'Lato', sans-serif;
    word-spacing: 2px;
    color: #66fcf1;
  }
  @-webkit-keyframes text {
    0%{
      color: #0B0C10;;
      margin-bottom: -40px;
    }
    30%{
      letter-spacing: 25px;
      margin-bottom: -40px;
    }
  }
  @keyframes text {
    0%{
      color: #0B0C10;;
      margin-bottom: -40px;
    }
    30%{
      letter-spacing: 25px;
      margin-bottom: -40px;
    }
  }
